import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Redirect, Route, Switch } from "react-router";
import PageShell from "../../UI/PageShell/PageShell";
import Sidebar from "../../UI/Sidebar/Sidebar";
import Add from "./Add/Add";
import Profile from "./Profile/Profile";
import "./Superadmin.css";
import View from "./View/View";

const SuperAdmin = (props) => {
  // useEffect(() => {
  //   if (props.auth) {
  //     props.history.replace("/add");
  //   } else {
  //     props.history.replace("/auth");
  //   }
  // }, []);
  var superadminSidebar = [
    {
      dropdown: {
        heading: "PARTNERS",
        options: [
          { name: "ADD PARTNER", to: "/add" },
          { name: "VIEW PARTNER", to: "/view", initialActive: true },
        ],
      },
    },
    { name: "LOGOUT", to: "/logout" },
    {
      component: (
        <div
          className="nav-logo"
          style={{
            backgroundImage:
              "url(" +
              (props.logo !== null
                ? props.logo
                : require("../../../assets/images/Default-Profile.png")) +
              ")",
          }}
        ></div>
      ),
      to: "/profile",
    },
  ];

  return (
    <div>
      <Sidebar
        style={{ background: "transparent" }}
        showOnMobile={
          <div
            className="nav-logo-mobile"
            onClick={() => props.history.push("/profile")}
            style={{
              backgroundImage:
                "url(" +
                (props.logo !== null
                  ? props.logo
                  : require("../../../assets/images/Default-Profile.png")) +
                ")",
            }}
          ></div>
        }
        routes={superadminSidebar}
        heading={
          <img
            src={require("../../../assets/images/EZWalkin-Logo.jpg")}
            alt="logo"
          />
        }
      />
      <br />
      <Switch>
        <Route path="/profile" component={PageShell(Profile)} />
        <Route path="/add" component={PageShell(Add)} />
        <Route path="/view" component={PageShell(View)} />
        <Redirect to={props.auth ? "/view" : "/auth"} />
      </Switch>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.login.token !== null,
    logo: state.login.logo,
  };
};

export default connect(mapStateToProps)(SuperAdmin);
