import React, { useState } from "react";
import EachField from "../../../UI/FormField/FormField";
import "./Add.css";
import MyCard from "../../../UI/MyCard/MyCard";
// import SmallSpinner from "../../../UI/SmallSpinner/SmallSpinner";
import { axiosInstance } from "../../../Utility/axiosInstance";
import AsyncButton from "../../../UI/AsyncButton/AsyncButton";
import Alert from "../../../UI/Alert/Alert";
import csc from "country-state-city";

const Add = (props) => {
  const [formData, setFormData] = useState({
    name: "",
    address: "",
    country: "",
    email: "",
    contactPerson: "",
    contactNumber: "",
    latitude: "",
    longitude: "",
  });
  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [type, setType] = useState("success");

  const [loading, setLoading] = useState(false);

  const changeHandler = (event) => {
    const { name, value } = event.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const selectChangeHandler = (value, name) => {
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  var schema = [
    {
      name: "name",
      displayName: "Name",
      placeholder: "Enter Name",
      type: "text",
      value: formData.name,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "address",
      displayName: "Address",
      placeholder: "Enter Address",
      type: "textarea",
      value: formData.address,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "country",
      displayName: "Country",
      placeholder: "Enter Country",
      type: "select",
      value: formData.country,
      onChange: selectChangeHandler,
      options: [...csc.getAllCountries().map((el) => el.name)],
      required: true,
    },
    {
      name: "email",
      displayName: "Email",
      placeholder: "Enter Email",
      type: "email",
      value: formData.email,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "contactPerson",
      displayName: "Contact Person",
      placeholder: "Enter Contact Person",
      type: "text",
      value: formData.contactPerson,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "contactNumber",
      displayName: "Contact Number",
      placeholder: "Enter Contact Number",
      type: "number",
      value: formData.contactNumber,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "latitude",
      type: "number",
      displayName: "Latitude",
      placeholder: "Enter Latitude",
      value: formData.latitude,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "longitude",
      type: "number",
      displayName: "Longitude",
      placeholder: "Enter Longitude",
      value: formData.longitude,
      onChange: changeHandler,
      required: true,
    },
  ];

  const submitHandler = (event) => {
    event.preventDefault();
    setLoading(true);
    axiosInstance
      .post("/signup/partner", formData)
      .then((res) => {
        setLoading(false);
        setShow(true);
        setMessage("Success");
        setType("success");
      })
      .catch((err) => {
        setLoading(false);
        setShow(true);
        setMessage("Error");
        setType("error");
      });
  };

  const valid = () => {
    let requiredFeilds = [...Object.values(formData)];
    return requiredFeilds.every((el) => el.trim() !== "");
  };

  const hideAlert = () => {
    setShow(false);
    if (type !== "error") {
      setFormData({
        name: "",
        address: "",
        country: "",
        email: "",
        contactPerson: "",
        contactNumber: "",
        latitude: "",
        longitude: "",
      });
    }
  };
  console.log(navigator.geolocation);
  return (
    <MyCard title="ADD NEW PARTNER">
      <Alert type={type} message={message} hideAlert={hideAlert} show={show} />
      <form className="form" onSubmit={submitHandler}>
        {schema.map((el, index) => (
          <EachField key={index} {...el} />
        ))}
        <AsyncButton
          loading={loading}
          disabled={!valid()}
          className="bg-green lg"
          type="submit"
        >
          Create
        </AsyncButton>
      </form>
    </MyCard>
  );
};

export default Add;
